import React,{Component}  from 'react';
import '../Contact/Contact.css';
export default class Privacy extends Component{
    render(){
        return(
            <div className="content container">
            <div className="page-wrapper">
                <header className="page-heading clearfix">
                    <h1 className="heading-title pull-left">Privacy Policy</h1>
                    <div className="breadcrumbs pull-right">
                        <ul className="breadcrumbs-list">
                            <li className="breadcrumbs-label">You are here:</li>
                            <li><a href="/">Home</a><i className="fa fa-angle-right"></i></li>
                            <li className="current">Privacy Policy</li>
                        </ul>
                    </div>
                </header> 
                <div className="page-content">
                    <div className="row page-row">
                        <article className="welcome col-md-12 col-sm-12 text-justify">                                                    
							<p>This Privacy Policy applies to the Soundarya Composite PU college</p>
							<p>puc.soundaryainstitutions.in recognises the importance of maintaining your privacy. We value your privacy and appreciate your trust in us. This Policy describes how we treat user information we collect on http://puc.soundaryainstitutions.in/ and other offline sources. This Privacy Policy applies to current and former visitors to our website and to our customers. By visiting and/or using our website, you agree to this Privacy Policy.</p>
							<p>[http://puc.soundaryainstitutions.in/] is a property of [soundarya educational trust], an Indian Company registered under the Companies Act, 2013 having its registered office at [ Havanoor Extension, Road Post, Near 8th Mile,, Hesarghatta Main Rd, Nagasandra, Bengaluru, Karnataka 560073].</p>
							<h3 className="title"><b>Information we collect</b></h3>
							<p><b>Contact information:</b> We might collect your name, email, mobile number, phone number, street, city, state, pincode, country and ip address.</p>
							<p><b>Payment and billing information: </b>We might collect your billing name, billing address and payment method when you fill application form. We NEVER collect your credit card number or credit card expiry date or other details pertaining to your credit card on our website. Credit card information will be obtained and processed by our online payment partner.</p>
							<p><b>Information you post: </b>We collect information you post in a public space on our website or on a third party social media site belonging to puc.soundaryainstitutions.in </p>
							<p><b>Demographic information: </b>We may collect demographic information about you, events you like, events you intend to participate in, application you fill, or any other information provided by you during the use of our website. We might collect this as a part of a survey also.</p>
							<p><b>Other information: </b>If you use our website, we may collect information about your IP address and the browser you're using. We might look at what site you came from, duration of time spent on our website, pages accessed or what site you visit when you leave us. We might also collect the type of mobile device you are using, or the version of the operating system your computer or device is running.</p>
							<h3 className="title"><b>We collect information in different ways:</b></h3>
							<p><b>We collect information directly from you: </b>We collect information directly from you when you filling the admission form. We also collect information if you post a comment on our websites or ask us a question through phone or email.</p>
							<p><b>We collect information from you passively: </b>We use tracking tools like Google Analytics, Google Webmaster, browser cookies and web beacons for collecting information about your usage of our website.</p>
							<p><b>We get information about you from third parties: </b>For example, if you use an integrated social media feature on our websites. The third­party social media site will give us certain information about you. This could include your name and email address</p>
							<h3 className="title"><b>Use of your personal information</b></h3>
							<p><b>We use information to contact you: </b>We might use the information you provide to contact you for confirmation of a application on our website or for other promotional purposes. </p>
							<p><b>We use information to respond to your requests or questions: </b>We might use your information to confirm your application.</p>
							<p><b>We use information to improve our products and services: </b>We might use your information to customize your experience with us. This could include displaying content based upon your preferences.</p>
							<p><b>We use information to look at site trends and customer interests: </b>We may use your information to make our website and service better. We may combine information we get from you with information about you we get from third parties.</p>
							<p><b>We use information for security purposes: </b>We may use information to protect our company, our customers, or our websites.</p>
							<p><b>We use information for marketing purposes: </b>We might send you information about special promotions or offers.</p>
							<p><b>We might also tell you about new features or products: </b>These might be our own offers or services, or third­-party offers or services we think you might find interesting. Or, for example, if you fill application form for us we'll enroll you in our newsletter.</p>
							<p><b>We use information to send you transactional communications: </b>We might send you emails or SMS about your application form.</p>
							<p>We use information as otherwise permitted by law.</p>
							<h3 className="title"><b>Sharing of information with third­parties</b></h3>
							<p><b>We will share information with third parties who perform services on our behalf: </b>We share information with vendors who help us manage our online registration process or payment processors or transactional message processors. Some vendors may be located outside of India.</p>
							<p><b>We will share information with the event organizers: </b>We share your information with event organizers and other parties responsible for fulfilling the purchase obligation. The event organizers and other parties may use the information we give them as described in their privacy policies.</p>
							<p><b>We will share information with our business partners: </b>This includes a third party who provide or sponsor an event, or who operates a venue where we hold events. Our partners use the information we give them as described in their privacy policies.</p>
							<p><b>We may share information if we think we have to in order to comply with the law or to protect ourselves: </b>We will share information to respond to a court order or subpoena. We may also share it if a government agency or investigatory body requests. Or, we might also share information when we are investigating potential fraud.</p>
							<p><b>We may share information with any successor to all or part of our business: </b>For example, if part of our business is sold we may give our customer list as part of that transaction.</p>
							<p><b>We may share your information for reasons not described in this policy: </b>We will tell you before we do this. </p>
							<h3 className="title"><b>Cookies OptOut</b></h3>
							<p>You can opt out of our advertising cookies. To stop receiving our promotional ads, please visit Google optout page and uncheck accordingly <a style={{"color":"#3992C2","text-decoration":"underline"}} href="https://www.google.com/settings/u/0/ads/authenticated"> Optout Page</a>. It may take about 4 days to process your request. Alternatively, if you wish to optout of advertising from multiple networks <a style={{"color":"#3992C2","text-decoration":"underline"}} href="http://www.networkadvertising.org/choices/"> visit this page</a></p>
							<h3 className="title"><b>Third party sites</b></h3>
							<p>If you click on one of the links to third party websites, you may be taken to websites we do not control. This policy does not apply to the privacy practices of those websites. Read the privacy policy of other websites carefully. We are not responsible for these third party sites.</p>
							<h3 className="title"><b>Grievance Officer:</b></h3>
							<p>In accordance with Information Technology Act 2000 and rules made there under, the name and contact details of the Grievance Officer are provided below:</p>
							<p>Mr.  Keerthan Kumar</p>
							<p>[ Prakruthi Layout, Soundarya Layout, Bengaluru, Karnataka 560073]</p>
							<p>Phone 080-28390024: </p>
							<p>Email: soundaryainstitutions@gmail.com</p>
							<p>If you have any questions about this Policy or other privacy concerns, you can also email us at [soundaryainstitutions@gmail.com]</p>
							<h3 className="title"><b>Updates to this policy</b></h3>
							<p>This Privacy Policy was last updated on 15.03.2017. From time to time we may change our privacy practices. We will notify you of any material changes to this policy as required by law. We will also post an updated copy on our website. Please check our site periodically for updates.</p>
							<p><b>Jurisdiction</b></p>
							<p>If you choose to visit the website, your visit and any dispute over privacy is subject to this Policy and the website's terms of use. In addition to the foregoing, any disputes arising under this Policy shall be governed by the laws of India.</p>
						</article>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}



